import React from 'react';

const Quote=()=>{
    const chef_quotes=[
        'When you acknowledge, as you must, that there is no such thing as perfect food, only the idea of it, then the real purpose of striving toward perfection becomes clear: to make people happy, that is what cooking is all about.  - Thomas Keller',
        'We\'re hoping to succeed; we\'re okay with failure. We just don\'t want to land in between. - David Chang',
        'We go through our careers and things happen to us. Those experiences made me what I am. - Thomas Keller',
        'If you want to become a great chef, you have to work with great chefs. And that\'s exactly what I did. - Gordon Ramsay',
        'I always say that I don\'t believe I\'m a chef. I try to be a storyteller. - Jose Andres',
        'My philosophy from day one is that I can sleep better at night if I can improve an individual’s knowledge about food and wine, and do it on a daily basis. - Emeril Lagasse',
        'A great restaurant doesn\'t distinguish itself by how few mistakes it makes but by how well they handle those mistakes. - Danny Meyer',
        'To me, there\'s no great chef without a great team. - Daniel Boulud',
        'When you have made as many mistakes as I have then you can be as good as me. - Wolfgang Puck'
  
    ]
    return(
        <div className="quote_div">
            <cite className="quote_holder">
                "{chef_quotes[Math.floor(Math.random()*7)]}"
            </cite>
        </div>
    )
}

export default Quote;